import Vue from 'vue'
import VueRouter from 'vue-router'
import Fam from '../views/Fam.vue'
import Home from '../views/Home.vue'
import Team from '../views/Team.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      title: "CSUI2020 - Home"
    }
  },
  {
    path: '/fam',
    name: 'Fam',
    component: Fam,
    meta: {
      title: "CSUI2020 - Meet Our Fam"
    }
  },
  {
    path: '/team',
    name: 'Team',
    component: Team,
    meta: {
      title: "CSUI2020 - The Team"
    }
  }
]

const router = new VueRouter({
  mode: 'history',
  hashbag: true,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
  } else {
      return { x: 0, y: 0 }
  }
  },
  routes
})


export default router
